<template>
  <div>
    <el-dialog
      :title="addEditType === 'add' ? '添加系统' : '编辑系统'"
      :visible.sync="addDialogVisible"
      fullscreen
      center>
      <el-form
        :model="addForm"
        label-width="120px"
        ref="addForm"
        :rules="addFormRules"
        status-icon
        style="width: 40%; margin: auto;">
        <el-form-item label="名称" prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="路径" prop="url">
          <el-input v-model="addForm.url"></el-input>
        </el-form-item>
        <el-form-item label="图片" prop="picFile">
          <el-upload
            ref="upload"
            accept="image/png"
            action
            :on-change="fileChange"
            list-type="picture-card"
            :limit="1"
            :auto-upload="false">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="描述" prop="description">
          <el-input v-model="addForm.description" type="textarea" rows="5" resize="vertical"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="default" @click="addEditSystemCancel">取 消</el-button>
        <el-button type="primary" @click="addEditSystemConfirm">确 定</el-button>
      </div>
    </el-dialog>


    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="名称" prop="name">
        <el-input v-model="searchForm.name" placeholder="名称" @input="searchSystemList"></el-input>
      </el-form-item>
      <el-form-item label="启用状态" prop="disableFlag">
        <el-select v-model="searchForm.disableFlag" placeholder="启用状态" @change="searchSystemList">
          <el-option label="全部" value=""></el-option>
          <el-option label="可用" value="0"></el-option>
          <el-option label="禁用" value="1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchSystemList">查询</el-button>
        <el-button type="default" @click="searchFormReset">重置</el-button>
        <el-button type="success" @click="openSystemDialog('add')">添加</el-button>
      </el-form-item>
    </el-form>


    <el-table border :data="list" style="width: 100%">
      <el-table-column label="ID" width="40">
        <template slot-scope="scope">
          {{ scope.row.id }}
        </template>
      </el-table-column>

      <el-table-column label="名称" width="180">
        <template slot-scope="scope">
          <a :href="scope.row.url">{{ scope.row.name }}</a>
        </template>
      </el-table-column>

      <el-table-column label="图片" width="180">
        <template slot-scope="scope">
          <a :href="scope.row.picUrl" target="_blank">
            <img :src="scope.row.picUrl" width="24" height="24">
          </a>
        </template>
      </el-table-column>

      <el-table-column label="描述" width="200">
        <template slot-scope="scope">
          {{ scope.row.description }}
        </template>
      </el-table-column>

      <el-table-column label="启用状态" width="100">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.disableFlag===0" size="mini" type="info">正常</el-tag>
          <el-tag v-if="scope.row.disableFlag===1" size="mini" type="danger">禁用</el-tag>
        </template>
      </el-table-column>

      <el-table-column label="操作时间" width="180">
        <template slot-scope="scope">
          {{ scope.row.operateTime }}
        </template>
      </el-table-column>

      <el-table-column label="操作人" width="100">
        <template slot-scope="scope">
          {{ scope.row.operator }}
        </template>
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button plain type="primary" size="mini" @click="openSystemDialog('edit', scope.row)">编辑</el-button>
          <el-button plain type="danger" size="mini" @click="deleteSystem(scope.row.id)">删除</el-button>
          <el-button plain type="warning" size="mini" @click="disableSystem(scope.row.id)"
                     v-if="scope.row.disableFlag===0">禁用</el-button>
          <el-button plain type="primary" size="mini" @click="enableSystem(scope.row.id)"
                     v-if="scope.row.disableFlag===1">启用</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[100, 200, 300, 400]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
  import urls from '@/utils/urls';

  export default {
    name: "System",
    data() {
      return {
        addEditType: 'add',
        addDialogVisible: false,
        addForm: {
          name: '',
          url: '',
          description: '',
        },
        addFormRules: {
          name: [
            {required: true, message: '请输入系统名称', trigger: 'blur'},
            {min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur'}
          ],
          url: [
            {required: true, message: '请输入系统路径', trigger: 'blur'},
            {min: 8, max: 50, message: '长度在 8 到 50 个字符', trigger: 'blur'}
          ],
          /*picFile: [
            {required: true, message: '请上传图片', trigger: 'change'},
          ],*/
          description: [
            {min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur'}
          ]
        },

        searchForm: {
          name: '',
          disableFlag: ''
        },

        list: null,
        currentPage: 1,
        pageSize: 100,
        total: 1,
      }
    },
    mounted() {
      this.searchSystemList();
    },
    methods: {
      fileChange(file) {
        if (file.raw.type !== 'image/png') {
          this.$message.error('上传图片只能是 png 格式!');
          return;
        }
        if (file.raw.size > 1024 * 100) {
          this.$message.error('上传图片不能大于100KB!');
          return;
        }
        this.addForm.multipartFile = file.raw;
        return true;
      },


      searchSystemList() {
        this.$getLoading(urls.searchSystemList, {
          currentPage: this.currentPage,
          pageSize: this.pageSize,
          ...this.searchForm
        }).then((response) => {
          let {data} = response;
          this.list = data.list;
          this.total = data.total;
        })
      },
      searchFormReset() {
        this.$refs.searchForm.resetFields();
        this.searchSystemList();
      },

      openSystemDialog(type, row) {
        this.addDialogVisible = true;
        this.addEditType = type;
        if (row) {
          this.addForm = {
            id: row.id,
            name: row.name,
            url: row.url,
            description: row.description,
          };
        }
      },
      addEditSystemCancel() {
        this.addDialogVisible = false;
        this.$refs.addForm.resetFields();
      },
      addEditSystemConfirm() {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            if (this.addEditType === 'add' && !this.addForm.multipartFile) {
              this.$message.error('添加系统时，上传图片不能为空!');
              return;
            }
            this.$postFileLoading(this.addEditType === 'add' ? urls.addSystem : urls.editSystem, {...this.addForm}).then((response) => {
              this.addEditSystemCancel();
              this.searchSystemList();
            })
          } else {
            return false;
          }
        })
      },
      deleteSystem(systemId) {
        this.$confirm('确定删除该系统？', '危险', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'error'
        }).then(() => {
          this.$post(urls.deleteSystem, {systemId}).then((response) => {
            this.searchSystemList();
          })
        });
      },
      disableSystem(systemId) {
        this.$confirm('确定禁用该系统？', '警告', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$post(urls.disableSystem, {systemId}).then((response) => {
            this.searchSystemList();
          })
        });
      },
      enableSystem(systemId) {
        this.$post(urls.enableSystem, {systemId}).then((response) => {
          this.searchSystemList();
        })
      },


      handleSizeChange() {

      },
      handleCurrentChange() {

      }
    }
  }
</script>

<style scoped lang="scss">

</style>
